<template>
    <div class="nav-bar">
        <div class="nav-content">
            <!-- 左侧logo -->
            <section class="logo">
                <div class="logo-text">合并报表</div>
            </section>

            <!-- 右侧用户操作菜单按钮区域 -->
            <section class="action-menu">
                <ul>
                    <li>
                        <el-button
                            class="my-button3"
                            style="color: #fff;"
                            icon="bdo-icon-rollback"
                            type="text"
                            @click="goBack"
                        >
                            返回</el-button
                        >
                    </li>
                    <li>
                        <el-button
                            class="my-button3"
                            style="color: #fff;"
                            icon="el-icon-s-home"
                            type="text"
                            @click="goHome"
                            >首页</el-button
                        >
                    </li>
                    <li>
                        <el-dropdown class="my-dropdown" trigger="click">
                            <span
                                style="color: #fff;"
                                class="my-button3 el-dropdown-link"
                            >
                                <i class="el-icon-menu"></i>&nbsp;示例页面
                                <i
                                    class="el-icon-arrow-down el-icon--right"
                                ></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item
                                    v-for="(item, index) in demoPages"
                                    :key="index"
                                    :command="item.route"
                                >
                                    <template
                                        ><span @click="goDemoPages(item)">{{
                                            item.label
                                        }}</span></template
                                    >
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </li>
                </ul>
            </section>
        </div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {
        absolute: { type: Boolean, default: false },
    },
    data() {
        return {
            systemMenus: [],
            demoPages: [{ label: '示例页面1', route: '/demo' }],
        };
    },
    computed: {},
    watch: {},
    methods: {
        goBack() {
            this.$router.go(-1);
        },
        goHome() {
            this.$router.push('/');
        },
        goDemoPages(item) {
            this.$router.push(item.route);
        },
    },
    created() {},
    mounted() {},
};
</script>

<style lang="scss" scoped>
.nav-bar {
    width: 100%;
    height: 56px;
    line-height: 56px;
    background-color: #1890ff;
    color: white;

    .nav-content {
        width: 100%;
        height: 56px;
        line-height: 56px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        padding: 0 32px 0 0;
    }

    .logo {
        height: 100%;
        white-space: nowrap;
        padding-left: 32px;

        .logo-text {
            width: 200px;
            display: inline-block;
            font-size: 18px;
            font-weight: bold;
            vertical-align: middle;
            box-sizing: border-box;
            // padding-left: 16px;
        }
    }

    .action-menu {
        ul {
            margin: 0;
            padding: 0;
            white-space: nowrap;

            li {
                list-style: none;
                display: inline-block;
                padding: 0 10px;
            }

            .info {
                font-size: 14px;
                color: #409eff;
            }
        }
    }
}
</style>

<style lang="scss">
.my-dropdown.el-dropdown {
    color: #409eff !important;
    cursor: pointer;
}

.el-breadcrumb__inner {
    color: rgb(202, 219, 255) !important;
}

.is-link {
    /*color: rgb(7, 95, 184)!important;*/
    color: rgb(202, 219, 255) !important;
}

.my-button3:hover {
    color: rgb(160, 204, 255) !important;
}
</style>



