<template>
    <div class="container flowbg">        
        <div class="wrapper">
            <router-view></router-view>
            <br/><br/><br/><br/><br/>
            <copyright color="#C0C4CC"></copyright>
        </div>
    </div>
</template>

<script>
import flowbg from '../../effects/css3/flowbg/flowbg.js';

import Copyright from '../../modules/Copyright.vue';
import NavBar from '../../modules/NavigationBar.vue';

export default {
    components: { NavBar, Copyright },
    data() {
        return {

        };
    },
    methods: {

    },
    mounted() {
        flowbg(require('../../assets/images/cloud.png'));
    }
}
</script>

<style lang="scss" scoped>
    .container {
        width: 100%;
        height: 100%;
        text-align: center;
        position: relative;

        .wrapper {
            position: absolute;
            top: 30%;
            left: 50%;
            transform: translate(-50%, 0);
        }
    }
</style>


